import React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import currentTask from "./currentTask.js";
import completedTask from "./completedTask.js";
import "./App.css";

function App() {
  return (
    <Router>
      <div className="App">
        <div className="app__header">
          <div className="app__headerAppName">Task Manager</div>
          <Link className="app__headerInfo" to="/">
            Current Tasks
          </Link>
          <Link className="app__headerInfo" to="/completedTask">
            Completed Tasks
          </Link>
        </div>
        <div className="app__body">
          <Route path="/completedTask" component={completedTask}></Route>
          <Route exact path="/" component={currentTask}></Route>
        </div>
      </div>
    </Router>
  );
}

export default App;
